import {Component, OnInit} from '@angular/core';

import {Store} from './store';

declare const Baas: any;

@Component({
  selector: 'my-store-list',
  templateUrl: './store-list.component.html',
  styleUrls: ['./store-list.component.css']
})
export class StoreListComponent implements OnInit {
  stores: Store[] = [];

  ngOnInit(): void {
    this.getObject();
    
  }

  getObject(): void {
    Baas.API_KEY = '6f25e0bc-f527-4bd0-824d-fcad7c4f2e91';
    let table = Baas.Table.create('store');
    
    table.fetch({
      success: this.onSuccess.bind(this),
      error: this.onError.bind(this)
    });
  }
  
  onSuccess(model, response): void {
    console.log(response);
    this.stores = response.results;
    
  }
  
  onError(model, response): void {
    
  }
}




